:root {
  --orange: #f87719;
  --black: #111;
  --second-black: #282828;
  --gray: #595959;
  --light-gray: #b7b7b7;
  --white: #f8f8f8;
  --white-smoke: #fff;
  --liner: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  --revers-liner: linear-gradient(141.22deg, #f84119 9.4%, #f89f19ad 91.91%);
  --transition-dur: .25s;
  --transition-func: cubic-bezier(.4, 0, .2, 1);
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
  background: none;
  border: none;
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

*, :before, :after {
  box-sizing: border-box;
}

body {
  letter-spacing: .02em;
  color: var(--white);
  background-color: var(--black);
  overflow: overlay;
  transition: background-color var(--transition-func) var(--transition-dur);
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
}

body.light-theme {
  background-color: var(--white-smoke);
  transition: background-color var(--transition-func) var(--transition-dur);
}

.visually-hidden {
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

