  border-radius: 20px;
  flex-direction: row;
  align-items: center;
  animation-fill-mode: forwards;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 1px 1px 14px 4px #ff6b086b;
}

